.customBtnWrapper{background-color:#0b0a33;cursor:pointer;border:1px solid #fff;padding:10px 30px;border-radius:20px;transition:.25s ease-out;font-family:"DM Sans",sans-serif;font-weight:bold;transform-style:preserve-3d;margin:10px}.customBtnWrapper:active{transform:scale(0.9)}.customBtnWrapper:after{top:-100%;left:0;width:100%;position:absolute;padding:10px 30px;border-radius:20px;transform-origin:left bottom;transform:rotateX(90deg)}.customBtnWrapper:hover{transform-origin:center bottom;transform:rotateX(-90deg) translateY(100%)}.variantRed:after{background:#6d0808;content:"Удалить"}.variantGreen:after{background:#3a9999;content:"Готово!"}