@import '../../../scss/variables';

.contentWrapper {
	display: flex;
	flex-direction: column;
	justify-content: center;
	margin-right: 20px;
}
.todoTitle {
	font-size: 50px;
}

.todoDescription {
	cursor: pointer;
}

button {
	position: relative;
	display: inline-block;
	cursor: pointer;
	outline: none;
	border: 0;
	vertical-align: middle;
	text-decoration: none;
	background: transparent;
	padding: 0;
	font-size: inherit;
	font-family: inherit;
	&.learn_more {
		width: 17rem;
		height: auto;
		.circle {
			@include transition(all, 0.45s, cubic-bezier(0.65, 0, 0.076, 1));
			position: relative;
			display: block;
			margin: 0;
			width: 3rem;
			height: 3rem;
			background: rgb(150, 46, 46);
			border-radius: 1.625rem;
			.icon {
				@include transition(all, 0.45s, cubic-bezier(0.65, 0, 0.076, 1));
				position: absolute;
				top: 0;
				bottom: 0;
				margin: auto;
				background: $white;
				&.arrow {
					@include transition(all, 0.45s, cubic-bezier(0.65, 0, 0.076, 1));
					left: 0.625rem;
					width: 1.125rem;
					height: 0.125rem;
					background: none;
					&::before {
						position: absolute;
						content: '';
						top: -0.25rem;
						right: 0.0625rem;
						width: 0.625rem;
						height: 0.625rem;
						border-top: 0.125rem solid #fff;
						border-right: 0.125rem solid #fff;
						transform: rotate(45deg);
					}
				}
			}
		}
		.button_text {
			@include transition(all, 0.45s, cubic-bezier(0.65, 0, 0.076, 1));
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			padding: 0.75rem 0;
			margin: 0 0 0 1.85rem;
			color: $white;
			font-weight: 700;
			line-height: 1.6;
			text-align: center;
			text-transform: uppercase;
		}
	}
	&:hover {
		.circle {
			width: 100%;
			.icon {
				&.arrow {
					background: $white;
					transform: translate(1rem, 0);
				}
			}
		}
		.button-text {
			color: $white;
		}
	}
}
